.main {
  width: 100%; }
  .main div section:first-child, .main div section:last-child {
    background-color: #330670; }
  .main div section:nth-child(2) {
    background-color: #1f0049; }
  .main article {
    width: 60%; }
  .main aside {
    width: 40%;
    background: linear-gradient(180deg, #dc6795 35%, #e76b61 100%); }

.progression_labeur_left {
  padding-left: 10% !important; }

@media screen and (min-width: 1024px) and (max-width: 1440px) {
  h1 {
    font-size: 70px; }
  h2 {
    font-size: 70px; }
  h3 {
    font-size: 20px; } }

@media only screen and (max-width: 1000px) {
  h1 {
    font-size: 50px; }
  p {
    font-size: 17px; }
  .main article, .main aside {
    width: 100%; }
  .progression_labeur_left {
    padding-left: 0% !important; } }
